:root {
  --black-100: #1c1c21;
  --black-40: #d0d0d8;
  --black-5: #f5f5f7;
  --white-100: #ffffff;
  --orange-5: #fff3eb;
  --orange-80: #ffac70;
  --orange-100: #ff8b37;
  --orange-120: #e87928;
  --green-100: #47d867;
  --green-5: #e6faea;
  --red-100: #ec6259;
  --red-5: #fdeeed;
  --container-padding-mobile: 16px;
}

body {
  color: var(--black-100);
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
}

.container {
  height: 100vh;
  min-height: 600px;
}

.icon svg path {
  fill: currentColor;
}

.icon svg {
  width: 1em;
  height: 1em;
}

.btn-primary {
  cursor: pointer;
  display: inline-block;
  box-sizing: border-box;
  color: var(--white-100);
  font-size: 1.25em;
  font-weight: 600;
  line-height: 116%;
  width: 14.8em;
  background-color: var(--orange-100);
  border-color: var(--orange-100);
  border: 1px solid transparent;
  padding: 1em 1.2em;
  border-radius: 0.6em;
  outline: 0;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.btn-primary:hover {
  background-color: var(--orange-80);
  border-color: var(--orange-80);
}

.btn-primary:active {
  background-color: var(--orange-120);
  border-color: var(--orange-120);
}

.only-mobile {
  display: none;
}

@media screen and (min-width: 120em) {
  body {
    font-size: 2em;
  }
}

@media screen and (min-width: 160em) {
  body {
    font-size: 2.5em;
  }
}

@media screen and (max-width: 48em) {
  .only-mobile {
    display: flex;
  }
}

@media screen and (max-width: 35.5em) {
  .container {
    min-height: auto;
  }

  .btn-primary {
    font-size: 14px;
    padding: 16px 24px;
  }
}
